import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)


const routes = [{
        path: '/',
        name: 'home',
        component: HomeView
    },
    {
        path: '/about',
        name: 'about',
        component: () =>
            import ( /* webpackChunkName: "about" */ '../views/AboutView.vue')
    },
    {
        path: '/lostfound',
        name: 'lostfound',
        component: () =>
            import ('../views/services/LostFound.vue')
    },
    {
        path: '/flightinformation',
        name: 'flightinformation',
        component: () =>
            import ('../views/flights/FlightInformation.vue')
    },
    {
        path: '/demo',
        name: 'demo',
        component: () =>
            import ( /* webpackChunkName: "about" */ '../views/demo.vue')
    },

    {
      path: '/details/:Pid',
      name: 'details',
      //component: details
      component: () =>
            import ('../views/details.vue')
    },
    {
        path: '/trackflights/',
        name: 'trackFlights',
        component: () =>
            import ('../views/flights/trackflights.vue')
    },
    {
        path: '/DutyFree',
        name: 'dutyfree',
        component: () =>
            import ('../views/attheairport/DutyFree.vue')
    },
    {
        path: '/FoodandDrink',
        name: 'foodanddrink',
        component: () =>
            import ('../views/attheairport/FoodandDrink.vue')
    },
    {
        path: '/shopping',
        name: 'shoppingAt',
        component: () =>
            import ('../views/attheairport/shopping.vue')
    },
    {
        path: '/charmingChennai',
        name: 'charmingChennai',
        component: () =>
            import ('../views/flights/charmingChennai.vue')
    },
    {
        path: '/services',
        name: 'airportServices',
        component: () =>
            import ('../views/services/airportServices.vue')
    },
    {
        path: '/tofromairport',
        name: 'tofromairport',
        component: () =>
            import ('../views/toFrom/toFromAirport.vue')
    },
    {
        path: '/beforeyoufly',
        name: 'beforeyoufly',
        component: () =>
            import ('../views/beforeyoufly/beforeYoufly.vue')
    },
    {
        path: '/beforeyouflydeparture',
        name: 'beforeyouflydeparture',
        component: () =>
            import ('../views/beforeyoufly/beforeYouflyDeparture.vue')
    },
    {
        path: '/beforeyouflytransitguide',
        name: 'beforeYouflyTransitGuide',
        component: () =>
            import ('../views/beforeyoufly/beforeYouflyTransitGuide.vue')
    },
    {
        path: '/underconstruction',
        name: 'underconstruction',
        component: () =>
            import ('../views/underConstruction.vue')
    },
    {
        path: '/signin',
        name: 'SignIn',
        component: () =>
            import ('../views/SignIn.vue')
    },

]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router